<template>
  <v-main style="background-color: #00141d">
    <div class="d-md-flex d-none pa-8"></div>
    <div class="d-flex d-md-none pa-1"></div>
    <div class="d-flex d-md-none">
      <v-tabs show-arrows centered background-color="rgb(0, 40, 50)" dark>
        <v-tab :to="{ name: 'AccountVerify' }"
          ><span class="tab-text">Account Verification</span></v-tab
        >
        <v-divider class="mx-4" vertical inset></v-divider>
        <v-tab :to="{ name: 'Wallet' }" inset
          ><span class="tab-text">Wallet Setting</span></v-tab
        >
        <v-divider class="mx-4" vertical inset></v-divider>
        <v-tab :to="{ name: 'PwdChange' }"
          ><span class="tab-text">Password Change</span></v-tab
        >
        <v-divider class="mx-4" vertical inset></v-divider>
        <v-tab :to="{ name: 'Record' }"
          ><span class="tab-text">Transaction Record</span></v-tab
        >
      </v-tabs>
    </div>
    <v-container>
      <v-row justify="center">
        <v-col cols="12">
          <v-row>
            <v-col cols="12">
              <v-card
                height="auto"
                class="rounded-xl"
                style="background-color: #002832"
              >
                <v-card-text>
                  <p
                    :class="[$vuetify.breakpoint.smAndDown ? 'title' : 'title']"
                    class="white--text"
                  >
                    Personal Information
                  </p>
                  <v-row>
                    <v-col cols="12">
                      <span class="subtitle-1 white--text">name</span>
                      <br />
                      <v-row>
                        <v-col cols="12" md="8" class="pb-0">
                          <v-text-field
                            ref="Name"
                            class="ml-auto mt-1"
                            dark
                            single-line
                            outlined
                            dense
                            :value="userName"
                            label="Please enter the name"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="4">
                          <v-btn
                            color="primary"
                            class="mt-1"
                            @click="changeName()"
                          >
                            modify
                          </v-btn>
                        </v-col>
                      </v-row>
                    </v-col>
                    <v-col cols="12" md="6">
                      <span class="subtitle-1 white--text">Wallet Balance</span>
                      <br />
                      <span
                        :class="[
                          $vuetify.breakpoint.smAndDown
                            ? 'subtitle-1 font-weight-bold'
                            : 'title',
                        ]"
                        class="mt-2 white--text"
                        >$ {{ WalletBalance }} USDT</span
                      >
                      <v-btn
                        v-if="pause == 1"
                        color="red"
                        text
                        @click="rework()"
                        >Insufficient balance</v-btn
                      >
                    </v-col>
                    <v-col cols="12" md="6" class="pt-0">
                      <span class="subtitle-1 white--text">E-mail</span>
                      <br />
                      <span
                        :class="[
                          $vuetify.breakpoint.smAndDown
                            ? 'subtitle-1 font-weight-bold'
                            : 'title',
                        ]"
                        class="mt-2 white--text"
                        >{{ Email }}</span
                      >
                    </v-col>
                    <v-col cols="12" md="6" class="pt-0">
                      <span class="subtitle-1 white--text">wallet Address</span>
                      <br />
                      <span
                        :class="[
                          $vuetify.breakpoint.smAndDown
                            ? 'subtitle-1 font-weight-bold'
                            : 'title',
                        ]"
                        class="mt-2 white--text"
                        >{{ Wallet }}</span
                      >
                    </v-col>
                    <v-col cols="12" md="6" class="pt-0">
                      <span class="subtitle-1 white--text">Phone Number</span>
                      <br />
                      <span
                        :class="[
                          $vuetify.breakpoint.smAndDown
                            ? 'subtitle-1 font-weight-bold'
                            : 'title',
                        ]"
                        class="mt-2 white--text"
                        >{{ Phone }}</span
                      >
                    </v-col>
                    <v-col cols="12" md="6" class="pt-0">
                      <span class="subtitle-1 white--text">Output Value</span>
                      <br />
                      <span
                        :class="[
                          $vuetify.breakpoint.smAndDown
                            ? 'subtitle-1 font-weight-bold'
                            : 'title',
                        ]"
                        class="mt-2 white--text"
                        >{{ ETH }} ETH</span
                      >
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
            </v-col>
            <v-col cols="12" md="2" class="px-0">
              <v-card
                :max-width="$vuetify.application.top + 150 + 'px'"
                :miin-width="$vuetify.application.top + 50 + 'px'"
                class="d-md-flex d-none mx-auto rounded-xl text-center"
                style="background-color: #002832"
              >
                <v-list
                  color="transparent"
                  class="mx-auto"
                  :max-width="$vuetify.application.top + 100 + 'px'"
                  nav
                >
                  <v-list-item
                    v-for="({ name, routerName }, n) in links"
                    :key="n"
                  >
                    <v-list-item-content>
                      <v-btn
                        block
                        text
                        large
                        depressed
                        plain
                        :to="{ name: routerName }"
                      >
                        <v-list-item-title class="login-btn white--text">
                          {{ name }}
                          <v-divider class="mt-4 white"></v-divider>
                        </v-list-item-title>
                      </v-btn>
                    </v-list-item-content>
                  </v-list-item>
                </v-list>
              </v-card>
            </v-col>
            <v-col cols="12" md="10" class="mb-4">
              <router-view
                class="rounded-xl"
                style="background-color: rgb(0, 40, 50)"
              />
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
    <div class="text-center">
      <v-dialog v-model="nameChangeModal" hide-overlay persistent width="30%">
        <v-card
          class="error-card text-center"
          width="auto"
          height="100"
          style="background-color: #000000"
        >
          <div class="pt-9"></div>
          <v-card-text class="text-h6 white--text">
            Name has been changed
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog v-model="reworkModal" hide-overlay persistent width="30%">
        <v-card
          class="error-card text-center"
          width="auto"
          height="100"
          style="background-color: #000000"
        >
          <div class="pt-9"></div>
          <v-card-text class="text-h6 white--text">
            The device has restarted
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog v-model="cantworkModal" hide-overlay persistent width="30%">
        <v-card
          class="error-card text-center"
          width="auto"
          height="100"
          style="background-color: #000000"
        >
          <div class="pt-9"></div>
          <v-card-text class="text-h6 white--text">
            Insufficient balance, please recharge first
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>
    <div class="pa-16"></div>
    <div class="pa-16"></div>
  </v-main>
</template>

<script>
import { changeName, rework } from "../api";

export default {
  names: "Member",

  data: () => ({
    links: [
      {
        name: "Account Verification",
        routerName: "AccountVerify",
      },
      {
        name: "Wallet Setting",
        routerName: "Wallet",
      },
      {
        name: "Password Change",
        routerName: "PwdChange",
      },
      {
        name: "Transaction Record",
        routerName: "Record",
      },
      {
        name: "My Miner",
        routerName: "Myminer",
      },
    ],
    Name: "",
    nameChangeModal: false,
    pause: null,
    reworkModal: false,
    cantworkModal: false,
  }),
  created() {
    this.$store.dispatch("getUserData");
  },
  mounted() {
    this.pause = this.Pause;
  },
  computed: {
    userName() {
      return this.$store.state.userName;
    },
    Email() {
      return this.$store.state.email;
    },
    Phone() {
      return this.$store.state.phone;
    },
    Wallet() {
      return this.$store.state.wallet;
    },
    Pause() {
      return this.$store.state.pause;
    },
    ETH() {
      return Number(this.$store.state.ETH_Amt).toFixed(2);
    },
    WalletBalance() {
      return Number(this.$store.state.walletBalance).toFixed(2);
    },
  },
  methods: {
    changeName() {
      const params = new URLSearchParams();
      params.append("Nickname", this.$refs.Name.lazyValue);

      changeName(params)
        .then((result) => {
          if (result.data.success == true) {
            this.nameChangeModal = true;
            setTimeout(() => {
              this.nameChangeModal = false;
              location.reload();
            }, 2000);
          } else {
            console.log(result.data.errorMsg);
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    rework() {
      rework()
        .then((result) => {
          if (result.data.success == true) {
            this.reworkModal = true;
            setTimeout(() => (this.reworkModal = false), 2000);
          } else {
            this.cantworkModal = true;
            setTimeout(() => {
              this.cantworkModal = false;
              this.$router.push({ name: "Deposit" });
            }, 2000);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style lang="scss">
.login-btn {
  text-transform: capitalize;
  font-weight: normal;
  font-size: 16px;
}

.tab-text {
  text-transform: capitalize;
  font-weight: bold;
  font-size: 12px;
}

.error-card {
  background: #000000;
  opacity: 0.9;
}
</style>